<template lang="pug">
        anim
</template>
<script>
import anim from "../assets/images_src/anim.svg";
import { TweenLite, TweenMax } from "gsap";

export default {
  components: {
    anim
  },
  mounted() {
    this.svg_target = document.getElementById("intersection_anim");
    var dashArray = [{}];
    var brakeOnsetTime = 5;
    var crossingDuration = 4;
    var brakingTime = 2;

    TweenMax.set("#guides", { alpha: 0 });
    TweenMax.set("#body", { transformOrigin: "50% bottom" });

    const t2 = new TimelineMax({
      repeat: -1,
      repeatDelay: 1,
      ease: Power3.easeInOut
    });
    t2.fromTo("#body", 0.1, { y: "+=0" }, { y: "-=.25" }).to(
      "#body",
      0.1,
      { y: "+=.25" },
      0.1
    );
    t2.fromTo("#car", 3, { x: "-=3" }, { x: "+=5" })
      .repeat(-1)
      .yoyo(true);

    /* Automatically generate objects */
    var elRoad = document.getElementById("road");
    var elSides = document.getElementById("sides");

    /* Get the elements to duplicate */
    var elRoadDash = document.getElementById("road_dash");
    var elPublicLight = document.getElementById("public_light");
    var elHouseLeft = document.getElementById("house_left");
    var elHouseRight = document.getElementById("house_right");

    /* Number of elements to duplicate */
    var nrDashes = 20;
    var nrPublicLights = 10;
    var nrHouses = 10;

    /* Duplicate the elements */
    cloneEl(elSides, elHouseLeft, nrHouses);
    cloneEl(elSides, elHouseRight, nrHouses);
    cloneEl(elRoad, elRoadDash, nrDashes);
    cloneEl(elSides, elPublicLight, nrPublicLights);

    /* Remove the duplicated elements */
    elRoadDash.remove();
    elPublicLight.remove();
    elHouseLeft.remove();
    elHouseRight.remove();

    TweenMax.set("#sides", { x: 40, y: 109, transformOrigin: "50% bottom" });
    TweenMax.set(".road_dash", {
      scaleX: 0,
      scaleY: 0,
      transformOrigin: "50% 50%"
    });
    TweenMax.set(".public_light", {
      scaleX: 0,
      scaleY: 0,
      x: -40,
      y: -112,
      transformOrigin: "0 bottom"
    });
    TweenMax.set(".house_left", {
      scaleX: 0,
      scaleY: 0,
      x: 345,
      y: -182,
      transformOrigin: "0 bottom"
    });
    TweenMax.set(".house_right", {
      scaleX: 0,
      scaleY: 0,
      x: 347,
      y: -182,
      transformOrigin: "0 bottom"
    });
    TweenMax.set("#interaction", {
      scaleX: 0,
      scaleY: 0,
      x: 0,
      y: -41.5,
      transformOrigin: "50% bottom"
    });
    /* Initialize the TimelineMax for the road group */
    const t = new TimelineMax({ paused: true });

    /* Generate the Tweens*/
    // Define the moving path
    var dashesMovingPath = {
      type: "thru",
      curviness: 0,
      values: [
        { x: "+=0", y: "+=0", scaleX: 0, scaleY: 0 },
        { x: "-=200", y: "+=200", scaleX: 2, scaleY: 2 }
      ]
    };
    var publicLightsMovingPath = {
      type: "thru",
      curviness: 0,
      values: [
        { x: "+=0", y: "+=0", scaleX: 0, scaleY: 0 },
        { x: "-=485", y: "+=200", scaleX: 1.1, scaleY: 1.1 }
      ]
    };

    var housesLeftMovingPath = {
      type: "thru",
      curviness: 0,
      values: [
        { x: "+=0", y: "+=0", scaleX: 0, scaleY: 0 },
        { x: "-=1050", y: "+=200", scaleX: 2.75, scaleY: 2.75 }
      ]
    };

    var housesRightMovingPath = {
      type: "thru",
      curviness: 0,
      values: [
        { x: "+=0", y: "+=0", scaleX: 0, scaleY: 0 },
        { x: "+=725", y: "+=200", scaleX: 2.5, scaleY: -2.5 }
      ]
    };
    var interactionMovingPath = {
      type: "thru",
      curviness: 0,
      values: [
        { x: "+=0", y: "+=0", scaleX: 0, scaleY: 0 },
        { x: "+=0", y: "+=200", scaleX: 4.8, scaleY: 4.8 }
      ]
    };

    /* Add tweens to the timeline */
    addTween(
      t,
      "road_dash",
      nrDashes,
      dashesMovingPath,
      "50% 50%",
      Expo.easeIn,
      -1,
      false
    );
    addTween(
      t,
      "public_light",
      nrPublicLights,
      publicLightsMovingPath,
      "0 bottom",
      Expo.easeIn,
      1,
      false
    );
    addTween(
      t,
      "house_left",
      nrHouses,
      housesLeftMovingPath,
      "0 bottom",
      Expo.easeIn,
      1,
      true
    );
    addTween(
      t,
      "house_right",
      nrHouses,
      housesRightMovingPath,
      "0 bottom",
      Expo.easeIn,
      1,
      true
    );

    t.to("#interaction", brakeOnsetTime, {
      bezier: interactionMovingPath,
      transformOrigin: "50% bottom",
      ease: Expo.easeIn,
      delay: brakeOnsetTime
    });

    var duration = 0.5;
    animateWheels(duration);
    animateLeg("left_leg", duration, 0);
    animateLeg("right_leg", duration, -1);
    animateBack(duration);
    animateHead(duration);
    animateArm(duration);

    TweenMax.fromTo(
      t,
      brakingTime,
      { timeScale: 1 },
      {
        timeScale: 0,
        delay: brakeOnsetTime - brakingTime + 0.8,
        onComplete: function() {
          t2.pause();
        }
      }
    );

    /* Animate cyclist path */
    TweenMax.fromTo(
      "#cyclist",
      crossingDuration,
      { x: "+=100" },
      {
        x: "-=500",
        delay: brakeOnsetTime - brakingTime + 0.8
      }
    );

    t.addLabel("start", 2 * brakeOnsetTime);
    t.play("start");
    function animateWheels(duration) {
      TweenMax.fromTo(
        ".wheel",
        duration / 5,
        { transformOrigin: "center", rotation: 0 },
        { rotation: -10, ease: Linear.easeNone, repeat: -1 }
      );
    }
    function animateArm(duration) {
      TweenMax.set("#arm", {
        transformOrigin: "10% 90%",
        rotation: 0
      });
      TweenMax.to("#arm", duration / 2, {
        rotation: "+=5",
        repeat: -1,
        delay: (duration / 2) * 0.1,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function animateHead(duration) {
      TweenMax.set("#head", {
        transformOrigin: "60% 85%",
        rotation: 0
      });
      TweenMax.to("#head", duration / 2, {
        rotation: "-=5",
        repeat: -1,
        delay: (duration / 2) * 0.1,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function animateBack(duration) {
      TweenMax.set("#back", {
        transformOrigin: "60% 85%",
        rotation: 0
      });
      TweenMax.to("#back", duration / 2, {
        rotation: "+=3",
        repeat: -1,
        delay: (duration / 2) * 0.1,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function animateLeg(id, duration, delay) {
      TweenMax.set("#" + id, {
        transformOrigin: "80% 10%",
        rotation: -20
      });
      TweenLite.set("#" + id + ">.calf", {
        transformOrigin: "7% 18%",
        rotation: -10
      });

      TweenMax.to("#" + id, duration, {
        rotation: "+=40",
        repeat: -1,
        delay: delay * duration,
        yoyo: true,
        ease: Sine.easeInOut
      });

      TweenMax.to("#" + id, duration, {
        y: 0.1,
        repeat: -1,
        delay: (0.25 + delay) * duration,
        yoyo: true,
        ease: Sine.easeInOut
      });

      TweenMax.to("#" + id + ">.calf", duration, {
        rotation: "+=50",
        repeat: -1,
        delay: (0.75 + delay) * duration,
        yoyo: true,
        ease: Sine.easeInOut
      });
    }

    function cloneEl(parent, el, nr) {
      for (let index = 1; index < nr; index++) {
        var clonedEl = el.cloneNode(true);
        clonedEl.classList.add(clonedEl.id);
        clonedEl.removeAttribute("id");
        parent.appendChild(clonedEl);
      }
    }
    function addTween(
      timeline,
      elName,
      elNr,
      movingPath,
      transformOrigin,
      ease,
      repetitionNr,
      reoderEl
    ) {
      var el = document.getElementsByClassName(elName);
      for (let index = 0; index < el.length; index++) {
        timeline.to(
          el[index],
          brakeOnsetTime,
          {
            bezier: movingPath,
            transformOrigin: transformOrigin,
            ease: ease,
            repeat: repetitionNr,
            onRepeat: onRepeatFcn,
            onRepeatParams: ["{self}", reoderEl],
            onComplete: onComplete,
            onCompleteParams: ["{self}"]
          },
          el.length === 1 ? 0 : (-1 / (elNr - 1)) * brakeOnsetTime * index
        );
      }
    }
    function onRepeatFcn(a, reoderEl) {
      if (reoderEl) {
        a.target.parentNode.insertBefore(
          a.target,
          a.target.parentNode.firstChild
        );
      }
    }
    function onComplete(a) {
      a.target.remove();
    }
  }
};
</script>

<style scoped></style>
